	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import address from '@/views/address/list'
	import paixingbang from '@/views/paixingbang/list'
	import storeup from '@/views/storeup/list'
	import shangjia from '@/views/shangjia/list'
	import cart from '@/views/cart/list'
	import discusstushuxinxi from '@/views/discusstushuxinxi/list'
	import tushuchuku from '@/views/tushuchuku/list'
	import tushufenlei from '@/views/tushufenlei/list'
	import yonghu from '@/views/yonghu/list'
	import messages from '@/views/messages/list'
	import orders from '@/views/orders/list'
	import tushuruku from '@/views/tushuruku/list'
	import config from '@/views/config/list'
	import tushuxinxi from '@/views/tushuxinxi/list'
	import shangjiaRegister from '@/views/shangjia/register'
	import shangjiaCenter from '@/views/shangjia/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '首页',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '首页',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '修改密码',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/shangjiaCenter',
			name: '商家个人中心',
			component: shangjiaCenter
		}
		,{
			path: '/news',
			name: '图书资讯',
			component: news
		}
		,{
			path: '/address',
			name: '地址',
			component: address
		}
		,{
			path: '/paixingbang',
			name: '排行榜',
			component: paixingbang
		}
		,{
			path: '/storeup',
			name: '我的收藏',
			component: storeup
		}
		,{
			path: '/shangjia',
			name: '商家',
			component: shangjia
		}
		,{
			path: '/cart',
			name: '购物车',
			component: cart
		}
		,{
			path: '/discusstushuxinxi',
			name: '图书信息评论',
			component: discusstushuxinxi
		}
		,{
			path: '/tushuchuku',
			name: '图书出库',
			component: tushuchuku
		}
		,{
			path: '/tushufenlei',
			name: '图书分类',
			component: tushufenlei
		}
		,{
			path: '/yonghu',
			name: '用户',
			component: yonghu
		}
		,{
			path: '/messages',
			name: '留言板',
			component: messages
		}
		,{
			path: '/orders',
			name: '订单管理',
			component: orders
		}
		,{
			path: '/tushuruku',
			name: '图书入库',
			component: tushuruku
		}
		,{
			path: '/config',
			name: '轮播图',
			component: config
		}
		,{
			path: '/tushuxinxi',
			name: '图书信息',
			component: tushuxinxi
		}
		]
	},
	{
		path: '/shangjiaRegister',
		name: '商家注册',
		component: shangjiaRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
